import { createClient } from "@neondatabase/serverless";

const client = createClient({ /* your Neon connection */ });

export async function POST(req) {
  try {
    const { text, userId } = await req.json();
    if (!text || !userId) return new Response(JSON.stringify({ error: "Missing fields" }), { status: 400 });

    // Add to Neon
    const result = await client.query(
      "INSERT INTO goals (user_id, text) VALUES ($1, $2) RETURNING *",
      [userId, text]
    );

    return new Response(JSON.stringify({ goal: result[0] }), { status: 200 });
  } catch (err) {
    return new Response(JSON.stringify({ error: err.message }), { status: 500 });
  }
}
